
@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700|Raleway:400,700");
body {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  background-color: #F6F6F6;
  font-size: 16px;
  font-family: "Poppins";
  color: #242424;
  -webkit-font-smoothing: antialiased;
  position: relative;
}
@media (min-width: 800px) {
body {
      font-size: 18px;
}
}
@media (min-width: 1200px) {
body {
      font-size: 20px;
}
}
* {
  -webkit-tap-highlight-color: transparent;
}
*:focus {
  outline: none;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
h1 {
  font-weight: 600;
  margin: 0;
}
h2 {
  margin: 0;
}
.Intro {
  width: 100%;
  height: 100vh;
  background-color: white;
  z-index: 999;
  position: absolute;
  -webkit-animation-name: fadeOut;
          animation-name: fadeOut;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-delay: 1.5s;
          animation-delay: 1.5s;
  -webkit-animation-duration: 0.5s;
          animation-duration: 0.5s;
  pointer-events: none;
}
.Intro::after {
    position: absolute;
    content: "";
    top: 0;
    width: 100%;
    height: 100vh;
    background-color: #F6F6F6;
    -webkit-transform: translateX(-100vw);
            transform: translateX(-100vw);
    -webkit-animation: fadeIn;
            animation: fadeIn;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 0.8s;
            animation-duration: 0.8s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}
.Intro::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    height: 5px;
    background: #6E00FF;
    z-index: 2;
}
.Intro .Bg-Line {
    -webkit-animation-name: show;
            animation-name: show;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-transform: translateY(-100vh);
            transform: translateY(-100vh);
}
.Intro .Bg-Line:nth-child(0) {
      -webkit-animation-delay: calc(0 * 0.125s);
              animation-delay: calc(0 * 0.125s);
}
.Intro .Bg-Line:nth-child(1) {
      -webkit-animation-delay: calc(1 * 0.125s);
              animation-delay: calc(1 * 0.125s);
}
.Intro .Bg-Line:nth-child(2) {
      -webkit-animation-delay: calc(2 * 0.125s);
              animation-delay: calc(2 * 0.125s);
}
.Intro .Bg-Line:nth-child(3) {
      -webkit-animation-delay: calc(3 * 0.125s);
              animation-delay: calc(3 * 0.125s);
}
.Intro .Bg-Line:nth-child(4) {
      -webkit-animation-delay: calc(4 * 0.125s);
              animation-delay: calc(4 * 0.125s);
}
.Intro .Bg-Line:nth-child(5) {
      -webkit-animation-delay: calc(5 * 0.125s);
              animation-delay: calc(5 * 0.125s);
}
.Intro .Bg-Line:nth-child(6) {
      -webkit-animation-delay: calc(6 * 0.125s);
              animation-delay: calc(6 * 0.125s);
}
.Intro .Bg-Line:nth-child(7) {
      -webkit-animation-delay: calc(7 * 0.125s);
              animation-delay: calc(7 * 0.125s);
}
.Intro .Bg-Line:nth-child(8) {
      -webkit-animation-delay: calc(8 * 0.125s);
              animation-delay: calc(8 * 0.125s);
}
.Intro .Bg-Line:nth-child(9) {
      -webkit-animation-delay: calc(9 * 0.125s);
              animation-delay: calc(9 * 0.125s);
}
.Intro .Bg-Line:nth-child(10) {
      -webkit-animation-delay: calc(10 * 0.125s);
              animation-delay: calc(10 * 0.125s);
}
.Intro .Bg-Line:nth-child(11) {
      -webkit-animation-delay: calc(11 * 0.125s);
              animation-delay: calc(11 * 0.125s);
}
.Intro .Bg-Line:nth-child(12) {
      -webkit-animation-delay: calc(12 * 0.125s);
              animation-delay: calc(12 * 0.125s);
}
.Intro .Bg-Line:nth-child(13) {
