
@import url("https://fonts.googleapis.com/css?family=Poppins:300,400,500,600,700|Raleway:400,700");
body {
  width: 100%;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  background-color: #F6F6F6;
  font-size: 16px;
  font-family: "Poppins";
  color: #242424;
  -webkit-font-smoothing: antialiased;
  position: relative;
}
@media (min-width: 800px) {
body {
      font-size: 18px;
}
}
@media (min-width: 1200px) {
body {
      font-size: 20px;
}
}
* {
  -webkit-tap-highlight-color: transparent;
}
*:focus {
  outline: none;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}
h1 {
  font-weight: 600;
  margin: 0;
}
h2 {
  margin: 0;
}
.Contact {
  width: 100%;
  height: 80vh;
  background-color: #232323;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  overflow: hidden;
}
@media (max-width: 800px) {
.Contact {
      height: 70vh;
}
}
@media (max-width: 500px) {
.Contact {
      height: 40vh;
}
}
.Contact::before {
    content: "conta\00000act";
    position: absolute;
    font-size: 550px;
    line-height: 420px;
    font-weight: 700;
    color: rgba(255, 255, 255, 0.03);
    z-index: 0;
    pointer-events: none;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
@media (max-width: 1420px) {
.Contact::before {
      font-size: 450px;
      line-height: 380px;
}
}
@media (max-width: 1200px) {
.Contact::before {
      font-size: 350px;
      line-height: 320px;
}
}
@media (max-width: 800px) {
.Contact::before {
      font-size: 250px;
}
}
@media (max-width: 500px) {
.Contact::before {
      font-size: 120px;
      line-height: 120px;
}
}
.Contact .Section-Wrapper {
    max-width: 1000px;
    margin: 0 auto;
    padding: 25px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 2;
    position: relative;
}
.Contact .Section-Wrapper > span {
      font-weight: 600;
      color: #fff;
      display: block;
      margin-bottom: -10px;
}
@media (max-width: 1200px) {
.Contact .Section-Wrapper > span {
          margin-bottom: 10px;
}
}
.Contact .Section-Wrapper h1 {
      font-size: 70px;
      color: #242424;
}
@media (max-width: 1200px) {
.Contact .Section-Wrapper h1 {
          font-size: 50px;
}
}
@media (max-width: 800px) {
.Contact .Section-Wrapper h1 {
          font-size: 35px;
}
}
@media (max-width: 500px) {
.Contact .Section-Wrapper h1 {
          font-size: 30px;
}
}
.Contact .Section-Wrapper h1 span {
        display: inline-block;
}
.Contact .Section-Wrapper h2 {
      font-size: 70px;
      font-weight: 600;
      line-height: 100px;
      color: #fff;
}
.Contact .Section-Wrapper h2 a {
        color: #6E00FF;
}
@media (max-width: 1200px) {
.Contact .Section-Wrapper h2 {
          font-size: 50px;
}
}
@media (max-width: 800px) {
.Contact .Section-Wrapper h2 {
          font-size: 35px;
          line-height: 40px;
}
}
@media (max-width: 500px) {
.Contact .Section-Wrapper h2 {
          font-size: 30px;
}
.Contact .Section-Wrapper h2 a {
            margin-top: 10px;
            display: block;
}
}
.Contact .Bg-Line {
    background-color: rgba(255, 255, 255, 0.03);
}
@media (max-width: 800px) {
.Contact .Bg-Line {
        background-color: rgba(255, 255, 255, 0.01);
}
}
@media (max-width: 500px) {
.Contact .Bg-Line {
        background-color: rgba(255, 255, 255, 0.01);
}
}



